import { useQuota } from "@rp/react/tanstack-api/file"
import { useDeviceInfo } from "@rp/react/web/common/hooks"
import { Box, SxProps, Typography } from "@rp/react/web/mui/core"

type Props = {
  sx?: SxProps
  onClick?: () => void
  isFreemium?: boolean
}

export const QuotaReachedMessage = ({ sx, onClick, isFreemium }: Props) => {
  const { isMobile } = useDeviceInfo()
  const { isOverLimit } = useQuota()

  if (!isOverLimit) return null
  return (
    <Box
      sx={{
        backgroundColor: "overlay.surface",
        borderRadius: 2,
        width: "fit-content",
        px: 2,
        py: 1,
        ...sx,
      }}
    >
      <Typography fontSize={14}>
        You've reached your data analysis limit and canâ€™t upload more files.{" "}
        {!isFreemium && (
          <Typography
            onClick={onClick}
            component={"span"}
            sx={{
              fontSize: 14,
              display: "flex",
              direction: "row",
              color: "primary.main",
              whiteSpace: "nowrap",
              textDecoration: "none",
              "&:hover": {
                textDecoration: isMobile ? undefined : "underline",
              },
            }}
          >
            Upgrade your plan now.
          </Typography>
        )}
      </Typography>
    </Box>
  )
}
